@import url(https://fonts.googleapis.com/css?family=Abril+Fatface" rel="stylesheet">);
.createbox {
    display: flex;
    justify-content: space-between;
    width: 24em;
    background-color: #f2f2f2;
}

.createbutton {
    margin-top: 2em;
}
.familyaccount {
    margin-bottom: 3em;
    margin-top: 2em;
    width: 50em;
}

.accountbox {
    display: flex;
    justify-content: space-around;
    margin-top: .4em;
}

.acctitle {
    font-weight: bolder;
    display: flex;
}

.accsubtitle {
    font-weight: bold;
}

.accdetails {
    display: flex;
    flex-direction: column;
}


.accountlink {
    padding-top: 1.5em;
    align-self: flex-end;
    color: #67C2D0; 
}

.familyaccountbox {
    background-color: #f2f2f2;
}

.accountrow {
    display: flex;
}

h2 {
    margin-right: 2em;
    align-self: center;
    margin-bottom: 1em;
    font-size: 1.5em;
}
.header {
    display: flex;
    justify-content: center;
}

.header img {
    margin-left: 1em;
    width: 10em;
    height: 15em;
}

.title {
    margin-left: 2em;
    align-self: center;
}

.header h1 {
    font-size: 2em;
    font-family: 'Abril Fatface';
}
.homepage {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header {
    margin-top: 2em;
}

.createaccounts {
    margin-top: 4em;
    margin-bottom: 2.5em;
}
.control {
    margin-top: 2em;
    margin-bottom: 2em;
    display: flex;
    flex-direction: column;
}

.submitbutton {
    margin-top: 2em;
    align-self: flex-end;
}

.forminput1 {
    margin-bottom: 1em;
    width: 20em;
}


.tabletitles {
    background-color: #cccccc;
}
.viewtask {
    display: flex;
    justify-content: center;
}

.viewbox {
    margin-top: 4em;
    background-color: #f2f2f2
}

.viewtitle {
    margin-top: 2em;
    margin-bottom: 2em;
    font-weight: bolder;
}

.viewsubtitle {
    font-weight: bold;
    margin-bottom: 2em;
}



